/*开始设置*/
.ul-all>li {
    padding-right: 30px;
    -webkit-box-align: center;
}
.ui-list>li {
    position: relative;
    margin-left: 15px;
    line-height: 24px;
    display: -webkit-box;
}
/*02*/
.possword{
    margin-top: 15px;
    margin-bottom: 15px;
}
/*03*/
.exit{
    margin-top: 15px;
    margin-bottom: 15px;
}
.exit-span{
    display:block;
    background: #fff;
    width:100%;
    height:100%;
    text-align: center;
    padding-top: 12px;
    padding-bottom: 12px;
}
